import React, {Component} from 'react';
import axios from 'axios';
import Auxilliary from '../../hoc/Auxiliary/Auxilliary';
import Hero from '../../components/Hero/Hero';
import ContactForm from '../../components/ContactForm/ContactForm';
import Info from '../../components/Info/Info';


class Contact extends Component{
    state = {
        contact: {
            name: "",
            email: "",
            message: ""
        },

        submitted: false
    }

    onSubmitHandler = (event) => {
        event.preventDefault();
        axios.post('https://felseyventures.firebaseio.com/contact.json', this.state.contact).then(response => {
            console.log(this.state.contact);
            this.setState({submitted: true});
        })
        .catch(error => {
            alert('There was an error trying to submit this form. Try reloading the page, filling the form and submitting it.')
        });
        
    }

    nameChangedHandler = (event) => {
        const newState = {
            ...this.state.contact
        };

        newState.name = event.target.value;
        this.setState({contact: newState});
    }

    emailChangedHandler = (event) => {
        const newState = {
            ...this.state.contact
        };

        newState.email = event.target.value;
        this.setState({contact: newState});
    }

    messageChangedHandler = (event) => {
        const newState = {
            ...this.state.contact
        };

        newState.message = event.target.value;
        this.setState({contact: newState});
    }

    render() {
        let form =  <ContactForm 
                        nameChanged = {(event) => {this.nameChangedHandler(event)}}
                        emailChanged = {(event) => {this.emailChangedHandler(event)}}
                        messageChanged = {(event) => {this.messageChangedHandler(event)}} 
                        name = {this.state.name} 
                        email = {this.state.email}
                        message = {this.state.message} 
                        submit = {this.onSubmitHandler} />

        if(this.state.submitted){
            form = (
                <section className = "section-contact-form section-contact-form--submitted">
                    <div className = "contact__form contact__form--submitted">
                        <p className = "contact__paragraph">
                            <span className = "contact__span">Awesome! Your message has been delivered.</span>
                            <span className = "contact__span">As promised, we would get back to you very soon (at most three hours).</span>
                        </p>
                    </div>
                </section>
            );
        }
        return(
            <Auxilliary>
                <main className = "contact">
                    <Hero className = "contact__hero">Contact Us</Hero>
                    {form}
                    <Info />
                </main>
            </Auxilliary>
        );
    }
}

export default Contact;