import React, {Component} from 'react';

import Auxilliary from '../Auxiliary/Auxilliary';
import Toolbar from '../../components/Navigation/Toolbar/Toolbar';
import SideDrawer from '../../components/Navigation/SideDrawer/SideDrawer';
import Footer from '../../components/Layout Component/Footer/Footer';


class Layout extends Component{
    state = {
        showSideDrawer: false
    }

    sideDrawerToggleHandler = () => {
        this.setState((prevstate) => {
            return {showSideDrawer : !prevstate.showSideDrawer};    
        })
    }

    sideDrawerClosedHandler = () => {
        this.setState({showSideDrawer: false});
    }

    render(){
        return(
            <Auxilliary>
                <Toolbar toggleDrawerClicked = {this.sideDrawerToggleHandler} sideDrawerShown = {this.state.showSideDrawer} />
                <SideDrawer open = {this.state.showSideDrawer} closed = {this.sideDrawerClosedHandler} />
                <Auxilliary>
                    {this.props.children}
                </Auxilliary>
                <Footer />
            </Auxilliary>
        );
    }
}

export default Layout;